@import url(https://fonts.googleapis.com/css2?family=Jost:wght@700&display=swap);

body{
  background-color: #858585;
  margin: 0;
  font-family:'Jost' ;
}



.Item-types{
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
  line-height: 43px;
  color: #FFFFFF;
  padding-left: 32px;
  padding-top: 55px;
}

.Item-container{
  display: flex;
  flex-direction: row;
  overflow-x: auto;
  overflow-y: hidden;
  padding-bottom: 20px;
  cursor: pointer;
}


.Item_view{
    height: 421px;
    width: 286px;
    margin-left: 73px;
    margin-top: 45px;
    background-color:#2E2D2D ;
    cursor: pointer;
}

.Item_Image{
    height: 326px;
    width: 286px;
}

.Price_view{
    display: flex;
    justify-content: space-between;

}
.Item_name{
    color: rgb(218, 217, 217);
    height: 25px;
    max-width: 188px;
    padding-left: 10px;
    margin-top: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

}

.Item_price{
    color: rgb(218, 217, 217);
    padding-left: 10px;
    font-size: 15px;
}

.Add_cart_btn{
    font-family: "Jost";
    height: 30px;
    margin-top: 10px;
    border-radius: 15px;
    border-color: transparent;
    margin-right: 10px;
    background-color:  #F2C2B7;
    
}
.Buy_btn{
    font-family: "Jost";
    height: 30px;
    margin-top: 10px;
    border-radius: 15px;
    margin-right: 10px;
    border-color: transparent;
    background-color:  #BFBFBF;

}

.Item_view:hover{
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
}

.hov:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.Item_name:hover{
    text-decoration: underline;
    cursor: pointer;
    
}

a:link { text-decoration: none; }


a:visited { text-decoration: none; }


a:hover { text-decoration: none; }


a:active { text-decoration: none; }
.NavBar{
    background-color: #D9D9D9;
    height: 150px;
    overflow:hidden;
    display: flex;
    flex-direction: row;
    justify-content:space-between;
  }
  
  .Title{
    font-size: 50px;
    margin-left: 50px;
    color: black;
    cursor: pointer;
  }

  .NavBar_Icons{
    display: flex;
    flex-direction: row;
    margin-right: 30px;
  }
  
  .Cart_image{
    max-height: 40px;
    max-width: 90px;
    margin-right: 40px;
    margin-top: 60px;
  
  }
  .User_image{
    max-height: 40px;
    max-width: 90px;
    margin-right: 40px;
    margin-top: 60px;
  }

  .Cart_image:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  
  .User_image:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }

  @media only screen and (max-width: 450px) {
    .Title {
      margin-left: 10px;
      margin-right: 20px;
    }
    .NavBar_Icons{
      margin-right: 0px;
    }
    .Cart_image{
      margin-right: 10px;
    }
  
  }
  

